ut-disabled-color;\r\n  border-color: $input-disabled-solid-color;\r\n}\r\n","/* Checkboxes\r\n   ========================================================================== */\r\n\r\n/* CUSTOM CSS CHECKBOXES */\r\nform p {\r\n  margin-bottom: 10px;\r\n  text-align: left;\r\n}\r\n\r\nform p:last-child {\r\n  margin-bottom: 0;\r\n}\r\n\r\n/* Remove default checkbox */\r\n[type=\"checkbox\"]:not(:checked),\r\n[type=\"checkbox\"]:checked {\r\n  position: absolute;\r\n  opacity: 0;\r\n  pointer-events: none;\r\n}\r\n\r\n// Checkbox Styles\r\n[type=\"checkbox\"] {\r\n  // Text Label Style\r\n  + label {\r\n    position: relative;\r\n    padding-left: 35px;\r\n    cursor: pointer;\r\n    display: inline-block;\r\n    height: 25px;\r\n    line-height: 25px;\r\n    font-size: 1rem;\r\n    user-select: none;\r\n  }\r\n\r\n  /* checkbox aspect */\r\n  + label:before,\r\n  &:not(.filled-in) + label:after {\r\n    content: '';\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 18px;\r\n    height: 18px;\r\n    z-index: 0;\r\n    border: 2px solid $radio-empty-color;\r\n    border-radius: 1px;\r\n    margin-top: 2px;\r\n    transition: .2s;\r\n  }\r\n\r\n  &:not(.filled-in) + label:after {\r\n    border: 0;\r\n    transform: scale(0);\r\n  }\r\n\r\n  &:not(:checked):disabled + label:before {\r\n    border: none;\r\n    background-color: $input-disabled-color;\r\n  }\r\n\r\n  // Focused styles\r\n  &.tabbed:focus + label:after {\r\n    transform: scale(1);\r\n    border: 0;\r\n    border-radius: 50%;\r\n    box-shadow: 0 0 0 10px rgba(0,0,0,.1);\r\n    background-color: rgba(0,0,0,.1);\r\n  }\r\n}\r\n\r\n[type=\"checkbox\"]:checked {\r\n  + label:before {\r\n    top: -4px;\r\n    left: -5px;\r\n    width: 12px;\r\n    height: 22px;\r\n    border-top: 2px solid transparent;\r\n    border-left: 2px solid transparent;\r\n    border-right: $radio-border;\r\n    border-bottom: $radio-border;\r\n    transform: rotate(40deg);\r\n    backface-visibility: hidden;\r\n  