-green;\n  }\n}\n\nul {\n  list-style-type: lower-roman;\n\n  &:not(.browser-default) {\n    padding-left: 30px;\n\n    & > li {\n      list-style-type: disc;\n      font-weight: 300;\n    }\n  }\n}\n\nstrong, b {\n  font-weight: 500;\n}\n","* {\n  box-sizing: border-box;\n}\n\n.container {\n  @media #{$medium-and-up} {\n    width: 95%;\n  }\n  @media #{$large-and-up} {\n    width: 70%;\n  }\n}\n\n.content {\n  position: relative;\n  z-index: 3;\n}\n\n.flex {\n  display: flex;\n\n  @media #{$small-and-down} {\n    flex-wrap: wrap;\n  }\n}\n\n.flex-center {\n  align-items: center;\n}\n\n.flex-end {\n  justify-content: flex-end;\n}\n\n.flex-bottom {\n  align-items: flex-end;\n}\n\n.flex-wrap {\n  flex-wrap: wrap;\n}\n\n.flex-grid {\n  display: flex;\n  flex-wrap: wrap;\n\n  .flex-item {\n    padding: 10px;\n  }\n\n  &.s1x {\n    .flex-item {\n      display: flex;\n      position: relative;\n\n      @media #{$xsmall-and-down} {\n        width: 100%;\n      }\n    }\n  }\n\n  &.m2x {\n    .flex-item {\n      display: flex;\n      position: relative;\n\n      @media #{$small-and-up} {\n        width: 50%;\n      }\n    }\n  }\n}\n\n.space-between {\n  justify-content: space-between;\n}\n\n.space-evenly {\n  justify-content: space-evenly;\n}\n\n.float-left {\n  @extend .left;\n}\n\n.float-center {\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.absolute-right {\n  position: absolute;\n  right: 0;\n}\n\n.relative {\n  position: relative;\n}\n\n.mega-link.mega-link {\n  display: block;\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  z-index: 5;\n}\n\n[type=\"checkbox\"]:not(:checked), [type=\"checkbox\"]:checked {\n  position: static;\n  opacity: 1;\n  pointer-events: auto;\n}\n\n.wpcf7-checkbox {\n  column-count: 2;\n  display: block;\n}\n\nspan.wpcf7-list-item {\n  display: block !important;\n}\n\n@media #{$xsmall-and-down} {\n  .row .col.xs12 {\n    width: 100%;\n  }\n}\n\nimg {\n  max-width: 100%;\n  height: auto;\n}\n","// Main Button Styling\n.btn {\n  margin: 10px 0;\n  background: $color-white;\n  height: 45px;\n  line-height: 43px;\n  font-size: $size-regular;\n  font-family: $font-headers;\n  position: relative;\n  transition: all 0.3s;\n  border: 2px solid white;\n  border-radius: 3px;\n\n  // Arrow for hover\n  &::after {\n    content: '';\n    position: absolute;\n    opacity: 0;\n    width: 21px;\n    height: 21px;\n    top: 9px;\n    right: 5px;\n    background-repeat: no-repeat;\n    background-size: contain;\n    background-position: center;\n    background-image: url('../images/icon-arrow.svg');\n    transition: opacity 0.3s;\n  }\n\n  &:hover, &:focus, &:active {\n    color: $color-white;\n\n    &::after {\n      opacity: 1;\n    }\n  }\n\n  // Primary styles\n  &-primary {\n    color: $color-white;\n    border-color: $color-green;\n    background: $color-green;\n\n    &:hover, &:focus, &:active {\n      color: $color-green;\n      background: $color-white;\n      border-color: $color-white;\n\n      &::after {\n        background-image: url('../images/icon-arrow-green.svg');\n      }\n    }\n  }\n\n  // Secondary styles\n  &-secondary {\n    color: $color-blue;\n    border-color: $color-blue;\n\n    &:hover, &:focus, &:active {\n      background: $color-blue;\n      border-color: $color-blue;\n    }\n  }\n\n  // Tertiary styles\n  &-tertiary {\n    color: $color-green;\n\n    &:hover, &:focus, &:active {\n      border-color: $color-green;\n      background: $color-green;\n    }\n  }\n\n  // Tertiary styles\n  &-tertiaryInvert {\n    color: $color-blue;\n\n    &:hover, &:focus, &:active {\n      border-color: $color-blue;\n      background: $color-blue;\n      color: $color-white;\n    }\n  }\n\n  // Client Portal buttons\n  &-client {\n    height: auto;\n    line-height: 30px;\n    font-size: 1rem;\n    padding: 0 10px;\n    margin: 0;\n\n    &:hover, &:focus, &:active {\n      &::after {\n        opacity: 0;\n      }\n    }\n  }\n}\n","nav, .menu-main-menu-container {\n  background: