 {\n        background-image: url('../images/icon-book.svg');\n      }\n\n      &.icon-contact a::before {\n        background-image: url('../images/icon-contact.svg');\n      }\n\n      &.icon-list a::before {\n        background-image: url('../images/icon-list.svg');\n      }\n\n      &.icon-stars a::before {\n        background-image: url('../images/icon-stars.svg');\n      }\n    }\n\n    .menu-item-has-children {\n      position: relative;\n\n      a {\n        cursor: default;\n        border: none;\n\n        &:hover, &:focus, &:active {\n          border: none;\n        }\n      }\n\n      .sub-menu {\n        box-shadow: $drop-shadow;\n        border-radius: 0 0 $rounded-corners $rounded-corners;\n        z-index: 10;\n        background: $color-white;\n        color: $color-blue;\n        transition: max-height 0.6s ease;\n        height: 0;\n        max-height: 0;\n        overflow: hidden;\n        width: 200px;\n        text-align: center;\n        margin: 0;\n        top: 50px;\n        padding-left: 0;\n\n        li {\n          float: none;\n          list-style-type: none;\n          line-height: 1.4em;\n\n          &:first-of-type {\n            padding-top: 20px;\n          }\n\n          a {\n            color: $color-blue;\n            text-transform: uppercase;\n            font-weight: 300;\n            cursor: pointer;\n            padding: 0.75em 15px;\n\n            &:hover, &:focus, &:active {\n              background-color: rgba(0, 0, 0, 0.1);\n            }\n          }\n        }\n      }\n\n      &:hover, &:focus, &.hover {\n        .sub-menu {\n          height: auto;\n          max-height: 25em;\n        }\n      }\n    }\n\n    @media #{$large-and-up} {\n      .sub-menu {\n        flex-direction: column;\n        position: absolute;\n        left: 50%;\n        transform: translate3d(-50%, 0, 0);\n\n        &::before {\n          content: '';\n          position: absolute;\n          z-index: 1;\n          top: 0;\n          left: 